$main-font: 'Lobster', cursive;

html, body {
	margin: 0;
	padding: 0;
	font-family: Arial;
	background-color: #AE501D;
}

h1 {
	color: #FF8442;
	margin: 1em auto;
	margin-bottom: 2em;
	text-align: center;
	font-family: $main-font;
	font-size: 5em;
}

div.main {
	max-width: 700px;
	margin: 0 auto;

	p {
		padding: 1em;
		color: #FFC2A1;
	}

	aside {
		background: rgba(0,0,0,0.2);
		float: right;
		width: 33%;
		margin: 0em 1em 1em 1em;
		position: relative;

		h2 {
			color: #FF8442;
			padding-left: 0.66em;
			font-family: $main-font;
		}

		img {
			position: absolute;
			top: 0;
			left: 60px;
			width: 100px;
			transform: translate(0, -100%);
			filter: invert(1);
		}
	}
}